import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import huodongxinxiList from '@/views/pages/huodongxinxi/list'
import huodongxinxiDetail from '@/views/pages/huodongxinxi/formModel'
import huodongxinxiAdd from '@/views/pages/huodongxinxi/formAdd'
import huodongbaomingList from '@/views/pages/huodongbaoming/list'
import huodongbaomingDetail from '@/views/pages/huodongbaoming/formModel'
import huodongbaomingAdd from '@/views/pages/huodongbaoming/formAdd'
import storeupList from '@/views/pages/storeup/list'
import huodongfankuiList from '@/views/pages/huodongfankui/list'
import huodongfankuiDetail from '@/views/pages/huodongfankui/formModel'
import huodongfankuiAdd from '@/views/pages/huodongfankui/formAdd'
import shetuanxinxiList from '@/views/pages/shetuanxinxi/list'
import shetuanxinxiDetail from '@/views/pages/shetuanxinxi/formModel'
import shetuanxinxiAdd from '@/views/pages/shetuanxinxi/formAdd'
import shetuanfuzerenList from '@/views/pages/shetuanfuzeren/list'
import shetuanfuzerenDetail from '@/views/pages/shetuanfuzeren/formModel'
import shetuanfuzerenAdd from '@/views/pages/shetuanfuzeren/formAdd'
import shetuanfuzerenRegister from '@/views/pages/shetuanfuzeren/register'
import shetuanfuzerenCenter from '@/views/pages/shetuanfuzeren/center'
import forumList from '@/views/pages/forum/list'
import sheshiziyuanList from '@/views/pages/sheshiziyuan/list'
import sheshiziyuanDetail from '@/views/pages/sheshiziyuan/formModel'
import sheshiziyuanAdd from '@/views/pages/sheshiziyuan/formAdd'
import shenqingrusheList from '@/views/pages/shenqingrushe/list'
import shenqingrusheDetail from '@/views/pages/shenqingrushe/formModel'
import shenqingrusheAdd from '@/views/pages/shenqingrushe/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'huodongxinxiList',
			component: huodongxinxiList
		}, {
			path: 'huodongxinxiDetail',
			component: huodongxinxiDetail
		}, {
			path: 'huodongxinxiAdd',
			component: huodongxinxiAdd
		}
		, {
			path: 'huodongbaomingList',
			component: huodongbaomingList
		}, {
			path: 'huodongbaomingDetail',
			component: huodongbaomingDetail
		}, {
			path: 'huodongbaomingAdd',
			component: huodongbaomingAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'huodongfankuiList',
			component: huodongfankuiList
		}, {
			path: 'huodongfankuiDetail',
			component: huodongfankuiDetail
		}, {
			path: 'huodongfankuiAdd',
			component: huodongfankuiAdd
		}
		, {
			path: 'shetuanxinxiList',
			component: shetuanxinxiList
		}, {
			path: 'shetuanxinxiDetail',
			component: shetuanxinxiDetail
		}, {
			path: 'shetuanxinxiAdd',
			component: shetuanxinxiAdd
		}
		, {
			path: 'shetuanfuzerenList',
			component: shetuanfuzerenList
		}, {
			path: 'shetuanfuzerenDetail',
			component: shetuanfuzerenDetail
		}, {
			path: 'shetuanfuzerenAdd',
			component: shetuanfuzerenAdd
		}
		, {
			path: 'shetuanfuzerenCenter',
			component: shetuanfuzerenCenter
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'sheshiziyuanList',
			component: sheshiziyuanList
		}, {
			path: 'sheshiziyuanDetail',
			component: sheshiziyuanDetail
		}, {
			path: 'sheshiziyuanAdd',
			component: sheshiziyuanAdd
		}
		, {
			path: 'shenqingrusheList',
			component: shenqingrusheList
		}, {
			path: 'shenqingrusheDetail',
			component: shenqingrusheDetail
		}, {
			path: 'shenqingrusheAdd',
			component: shenqingrusheAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
	,{
		path: '/shetuanfuzerenRegister',
		component: shetuanfuzerenRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
